import { defineDocumentType, makeSource } from 'contentlayer/source-files';
import remarkFrontmatter from 'remark-frontmatter';
import remarkMdxFrontmatter from 'remark-mdx-frontmatter';

export const Guide = defineDocumentType(() => ({
    name: 'Guide',
    contentType: 'mdx',
    // Location of Post source files (relative to `contentDirPath`)
    filePathPattern: '**/*.mdx',
    // At the time of writing, we also have to define the `fields`
    // option to prevent an error on generation. We'll discuss
    // this option later. For now, we'll add an empty object.
    fields: {
        title: {
            type: 'string',
            required: true,
        },
        group: {
            type: 'string',
        },
        order: {
            type: 'number',
        },
        package: {
            type: 'string',
        },
        slug: {
            type: 'string',
            required: true,
        },
        category: {
            type: 'string',
        },
        description: {
            type: 'string',
        },
        import: {
            type: 'string',
            required: true,
        },
    },
}));

export default makeSource({
    // Location of source files for all defined documentTypes
    contentDirPath: 'docs',
    documentTypes: [Guide],
    mdx: {
        remarkPlugins: [remarkFrontmatter, remarkMdxFrontmatter],
    },
});
